export const tableHeader = {
  data() {
    return {
      tableColumn: [
        // {
        //   type: "checkbox",
        //   width: 44,
        //   align: 'center'
        // },
        {
          type: "seq",
          width: 44,
          align: 'center',
          title: '序号'
        },
        {
          title: '操作',
          width: 80,
          fixed: "right",
          slots: {
            default: (scope) => {
              return [
                <el-button type="text" size="small" disabled={this.authority.b_info != 1} onClick={() => { this.orderSee(scope) }}>查看</el-button>
              ]
            }
          }
        },
        // {
        //   field: 'order_sn',
        //   width: 156,
        //   title: '单据号',
        //   slots: {
        //     default: (scope, h) => {
        //       return [
        //         <a onClick={() => this.orderSee(scope, scope.row)} > {scope.row.order_sn} </a>
        //       ]
        //     }
        //   }
        // },
        {
          title: '退款编号',
          field: 'refund_sn',
          width: 150,
          slots: {
            default: (scope) => {
              return [
                <el-link disabled={this.authority.b_info != 1} onClick={() => this.orderSee(scope, scope.row)} > {scope.row.refund_sn} </el-link>
              ]
            }
          }
        },
        {
          title: '申请时间',
          field: 'add_time',
          formatter: ({ row }) => {
            return this.$publicFution.getAllTime(row.add_time * 1000)
          },
          width: 150
        },
        {
          title: '店铺',
          field: 'store_name',
          width: 150
        },
        {
          title: '退货类型',
          field: 'return_type',
          formatter: ({ row }) => {
            return Number(row.return_type) === 1 ? "不用退货" : "需要退货"
          },
          width: 150
        },
        {
          title: '订单编号',
          field: 'order_sn',
          width: 150
        },
        {
          title: 'ERP订单号',
          field: 'erp_order_sn',
          width: 170
        },
        {
          title: '商品名称',
          field: 'goods_name',
          minWidth: 300
        },
        {
          title: '退款金额(元)',
          field: 'refund_amount',
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: 'priceFormat'
        },
        {
          title: '退货数量(件)',
          field: 'goods_num',
          headerAlign: "right",
          align: "right",
          width: 100,
          formatter: 'priceFormat'
        },
        {
          title: '甲乙丙丁确认',
          field: 'seller_state',
          width: 120,
          formatter: ({ row }) => {
            // 卖家处理状态:1为待审核,2为同意,3为不同意
            var result = ''
            switch (Number(row.seller_state)) {
              case 3:
                result = "不同意";
                break;
              case 2:
                result = "同意";
                break;
              case 1:
                result = "待审核";
                break;

            }
            return result
          }
        },
        {
          title: '平台确认',
          field: 'admin_state_desc',
          width: 100,
        }
      ]
    }
  }
}