<template>
  <div class="params">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="申请时间:" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" value-format="yyyy-MM-dd" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="" size="mini" style="width: 320px;">
            <el-select v-model="params.type" class='typeSelect'>
              <el-option label="订单编号" value='order_sn'></el-option>
              <el-option label="退单编号" value='refund_sn'></el-option>
              <el-option label="商品名称" value='goods_name'></el-option>
            </el-select>
            <el-input v-model="params.key" maxlength="100" clearable @keyup.enter.native="onSearch(1)"></el-input>

          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <!-- <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="orderAdd" class="addBtn" size="mini">新增</el-button>
      </div> -->
      <xTable :ajax='{ url: "getServiceRefundList", params: params, result:"data.data.list" }' ref='xTable' :tableColumn='tableColumn' />
    </div>
  </div>
</template>

<script>
// order_sn 订单编号  refund_sn 退单编号   goods_name 商品名称
import { tableHeader } from "./config/index.js";
export default {
  name: "purchasePurchaserefundRefundRequest",
  mixins: [tableHeader],
  data() {
    return {
      authority: {},
      params: {
        type: "order_sn"
      },
      timeValue: []
    };
  },
  methods: {
    onReset() {
      this.timeValue = [];
      this.checkTime = [];
      this.params = {
        type: "order_sn"
      };
      /** 重置时 如需保存默认参数，则： */
      this.onSearch();
    },
    orderSee({ row }) {
      this.$router.push({
        path: "/purchase/refundRequest/refundRequestSee",
        query: { id: row.refund_id }
      });
    },
    onSearch(number) {
      if (this.timeValue) {
        this.params.add_time_to = this.timeValue[1];
        this.params.add_time_from = this.timeValue[0];
      } else {
        this.params.add_time_to = this.params.add_time_from = "";
      }
      this.$refs.xTable._getData(number);
    }
  },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>

<style scoped lang='less'>
.params {
  .typeSelect {
    /deep/.el-input {
      width: 100px;
    }
  }
}
</style>